.Navbar {
  background-color: white;
  width: 100%;
  height: 80px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.Navbar .containers {
  width: 80%;
  display: flex;
  justify-content: flex-end;
  align-items: center;
}

.Navbar h1{
  margin-right: auto;
}

.Navbar .log {
  display: block;
}


.Navbar .logs {
  background-color: rgba(54, 52, 49, 0.5);
  margin: 0 20px;
}

@media screen and (max-width: 768px) {
  .Navbar ul li:last-child {
    display: none;
  }

  .Navbar .logs {
    display: none;
  }

  .Navbar .containers {
    width: 100%;
    padding: 0px 10px;
  }

  .Navbar img {
    width: 170px;
    height: 50px;
    margin-right: auto;
  }
}

.cart {
