}

/* ///////////////////////// Navbar second //////////////////// */

.Navbars {
  width: 100%;
  height: 60px;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgb(20, 19, 19);
  padding: 5px 0px;
}

.Navbars .containerss {
  width: 80%;
  height: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}


.Navbars ul {
  color: white;
  padding-left: 0;
  margin-bottom: 0;
  margin-top: 0!important;
}

.Navbars ul li {
  list-style: none;
  display: inline-block;
  padding: 0 40px;
  font-size: 20px;
  text-transform: uppercase;
  font-family: "Montserrat", sans-serif;
  cursor: pointer;
}

.Navbars ul li a{
  text-decoration: none;
  color: black;
}

.searchBox {
  border-radius: 25px;
  background: #f0f3ff;
  display: flex;
  align-items: center;
  padding: 5px 0px;
}

.searchIcon {
  display: flex;
  justify-content: center;
  align-items: center;
}
.searchIcon svg {
  width: 50px;
  cursor: pointer;
}

.searchBox input {
  font-size: 10px;
  width: 0px;
  border: 0;
  background: transparent;
  outline: none;
  transition: 1s;
}

.inputb input {
  width: 300px;
  padding: 5px 2px;
}

.Navbars Button {
  display: none;
}

@media screen and (max-width: 968px){
  .Navbars .containerss {
    width: 90%;
    padding: 0px 10px;
  }
  .inputb input {
    width: 200px;
    padding: 5px 2px;
  }
}

@media screen and (max-width: 768px) {
  .Navbars ul {
    position: fixed;
    top: 140px;
    right: 100%;
    width: 100%;
    height: auto;
    color: white;
    background-color: black;
    margin: 0;
    flex-direction: column;
    padding: 20px 0px;
    z-index: 1;
    display: block;
    font-weight: bold;
    transition: 1.5s;
  }
  
  .Navbars ul li {
    display: block;
    width: auto;
    text-align: center;
    padding: 5px 0px;
  }
  .Navbars Button {
    display: block;
  }
  .ulsh ul {
    right: 0;
  }
  .inputb input {
    width: 150px;
    padding: 5px 2px;
  }
  .searchBox input {
    font-size: 8px;
  }
  .Navbars .containerss {
    width: 100%;
    padding: 0px 10px;
  }
}



.banner{
    width: 100%;
    height: 560px;
    background-image: linear-gradient(to left,rgba(240, 233, 233,0.4),rgba(0, 0, 0, 0.7)),url(/static/media/banner.c0ba2611.jpg);
    background-size: 100% 100%;
    background-position: center;
    position: relative;
}

.bannersec{
    width: 50%;
    position: absolute;
    left: 0;
    height: 100%;
    display: grid;
    place-items: center;
}

.headingban{
    width: 45%;
    height: 65%;
    background-color: black;
    box-shadow: rgba(255, 255, 255, 0.16) 0px 10px 36px 0px, rgba(255, 255, 255, 0.06) 0px 0px 0px 1px;
    display: grid;
    place-items: center;
    -webkit-animation: mymove 3s infinite;
            animation: mymove 3s infinite;
    color: white;
}

@-webkit-keyframes mymove{
    0%{
        margin-left:0px;
    }

    33%{
        margin-left: 30px;
    }

    67%{
        margin-left:-30px;
    }

    100%{
        margin-left:0px;
    }
}

@keyframes mymove{
    0%{
        margin-left:0px;
    }

    33%{
        margin-left: 30px;
    }

    67%{
        margin-left:-30px;
    }

    100%{
        margin-left:0px;
    }
}

.headingban h1{
    color: white;
}



@media screen and (max-width: 768px){
    .banner{
        height: 250px; 
    }
    .bannersec{
        width: 100%;
    }
}
.Service{
    width: 100%;
    height: auto;
}

.Serviceban h1{
    width: 100%;
    font-weight: bold;
    color: black;
    font-size: 3rem;
    display: inline-block;
    border-bottom: 1px solid black;
    
}

.Serviceban .card{
    background-color:black;
    color: white;
    display: grid;
    place-items: center;
    height: 400px;
    cursor: pointer;
}

.Serviceban .card .cardname{
    width: 80%;
    height: 70%;
    background-color: white;
    background-image: url(/static/media/paper.71e809eb.jpg);
    margin: 10px 0;
    padding: 25px 20px;
    position: relative;
}

.Serviceban .card .cardname:hover{
    -webkit-animation-name: twist;
            animation-name: twist;
    -webkit-animation-duration:1s;
            animation-duration:1s;
}

@-webkit-keyframes twist{
    0%{
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }

    25%{
        -webkit-transform: rotate(5deg);
                transform: rotate(5deg);
    }

    50%{
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }

    75%{
        -webkit-transform: rotate(-5deg);
                transform: rotate(-5deg);
    }

    100%{
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
}

@keyframes twist{
    0%{
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }

    25%{
        -webkit-transform: rotate(5deg);
                transform: rotate(5deg);
    }

    50%{
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }

    75%{
        -webkit-transform: rotate(-5deg);
                transform: rotate(-5deg);
    }

    100%{
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
}

.Serviceban .card .cardname h1{
    font-size: 2rem;
    border-bottom: none;
    text-transform: uppercase;
    display: inline-block;
    border-bottom: 2px solid black;
    text-align: center;
}

.Serviceban .card .cardname p{
    position: absolute;
    top: 110px;
    font-size: 1.1rem;
    border-bottom: none;
    color: black;
    text-align: center;
}


@media screen and (max-width: 768px){
    .Serviceban h1{
        font-size:  2rem;
        text-align: center;
    }

    .Serviceban .card{
        height: 300px;
    }

    .Serviceban .card .cardname h1{
        font-size: 30px;
    }

    .Serviceban .card .cardname p{
        font-size: 10px;
    }
}
